.paiMain {
  align-items: center;
  text-align: center;
  justify-content: center;
}
.t4 {
  width: 15em;
  color: white;
  border-radius: 20px;
  font-size: 1.8em;
  background: rgb(0, 0, 0, 0.5);
  margin: 1em;
  animation: moveLeftAndRight 5s linear infinite;
}
.testText {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.itensL {
  background: rgb(0, 0, 0, 0.5);
  text-align: left;
  border-radius: 20px;
  animation: moveLeftAndRight 5s linear infinite;
}

.contl {
  width: 15em;
  color: white;
  font-size: 1.5em;

  margin: 1em;
}

.rsEstudos {
  display: flex;
  align-items: center;
  justify-content: center;
}

.imgrms:hover {
  width: 22em;
}

.lgens {
  display: flex;
  align-items: center;
  justify-content: center;
  animation: moveLeftAndRight 7s linear infinite;
}

.inc {
  font-size: 3em;
  margin: 20px;
  color: #8a2be2;
  transition: 1s;
}

.inc:hover {
  font-size: 3.5em;
}

.t5 {
  width: 15em;
  color: white;
  border-radius: 20px;
  font-size: 1.8em;
  background: rgb(0, 0, 0, 0.5);
  animation: moveLeftAndRight 6s linear infinite;
}

.lg {
  width: 10em;
}
.conhecimentos {
  align-items: center;
  justify-content: center;
  text-align: center;
  display: flex;
}
@keyframes moveLeftAndRight {
  0% {
    transform: translateX(10);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateX(0);
  }
}

.imgrms {
  width: 20em;
  margin: 40px;
  border-radius: 100px 100px 10px 100px;
  animation: moveLeftAndRight 7s linear infinite;
  transition: 1s;
}
@media only screen and (max-width: 700px) {
  .rsEstudos {
    display: block;
    align-items: center;
    justify-content: center;
  }
  .inc {
    font-size: 2.5em;
    margin: 20px;
    color: white;
  }
  .conhecimentos {
    align-items: center;
    justify-content: center;
    text-align: center;
    display: block;
  }
  .t4,
  .t5 {
    width: 100%;
    color: white;
    border-radius: 20px;
    font-size: 1.5em;
    background: rgb(0, 0, 0, 0.5);
  }
  .contl {
    width: 90%;
    color: white;
    font-size: 1.4em;
    background: none;
    margin: none;
  }
}
