
@keyframes moveLeftAndRight {
  0% {
    transform: translateX(10);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateX(0);
  }
}

.paiHome {
  background-size: 50%;
  background-position-x: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.titulo{
  width: 50%;
}

.t1 {
  background-color: rgba(255, 255, 255, 0.1);
  font-size: 1.8em;
  color: blueviolet;
  border-radius: 20px;
  padding: 8px;
}


.t3 {
  background: rgba(255, 255, 255, 0.8);
  font-size: 1.8em;
  color: blueviolet;
  border-radius: 20px;
  padding: 8px;
}

.btninsta {
  margin: 20px;
  font-size: 1.5em;
  padding: 5px;
  border-radius: 10px 10px 10px 10px;
  background:transparent;
  color:white;
  border-color: #bebec0;
}
.glow-on-hover {
  font-weight: 800;
  font-size: 2em;
  width: 220px;
  height: 50px;
  border-radius: 20px 20px 20px 20px ;
  outline: none;
  color:white;
  background:transparent;
  cursor: pointer;
  position: relative;
  z-index: 0;
  margin: 10px;
  animation: moveLeftAndRight 10s linear infinite;
  opacity: 10;
  transition: 2s;
  box-shadow: 3px 3px 2px 2px rgb(0, 0, 0);
  border: none;
}
.glow-on-hover:hover {
  border: none;
  transition: 2s;
  color: #bebec0;
  
  font-size: 2.5em;
  box-shadow: none;
}

.glow-on-hover:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(8px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  border-radius: 10px;
}

.glow-on-hover:active:after {
  background: transparent;
}

.glow-on-hover:hover:before {
  opacity: 10;
}

.glow-on-hover:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  left: 0;
  top: 0;
  border-radius: 20px;
}
@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@keyframes moveLeftAndRight {
  0% {
    transform: translateX(10);
  }
  50% {
    transform: translateY(25px);
    
  }
  100% {
    transform: translateX(0);
  }
}
.t2 {
  width: 18em;
  color: white;
  font-size: 1.3em;
  margin: 10px;
  border-radius: 20px;
  background: rgb(0, 0, 0, 0.5);
  animation: moveLeftAndRight 5s linear infinite;
}

.animated-div {
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 1.8em;
  color: white;
  border-radius: 20px;
  padding: 8px;
  animation: moveLeftAndRight 5s linear infinite;
}



@media only screen and (max-width: 700px) {
  .paiHome {
    display: block;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .titulo {
    width: 100%;
  }
}
