.App {
  background-repeat: no-repeat;
  background-size: 100%;
  text-align: center;
  background-image: url(./components/img/astrotop.jpg);
  height: 100vh;
  position: fixed;
  overflow: auto;
  width: 100vw;
}


.container {
  width: 100%;
  height: 10px;
  background: blueviolet;
}
.stars {
  width: 3px;
  height: 3px;
  border-radius: 50%;
}
.stars {
  width: 3px;
  height: 3px;
  border-radius: 50%;
  box-shadow: 50px 30px white, 1000px 80px blueviolet, 1000px 100px white,
    800px 120px white, 80px 120px white, 300px 20px white, 300px 100px white,
    1110px 80px white, 1110px 100px white, 1300px 100px white, 600px 100px white,
    600px 2px white, 700px 100px white, 700px 1px white, 1000px 100px white,
    1500px 20px white, 1500px 30px white, 1500px 1px white, 1100px 20px white,
    1120px 20px white, 1120px 10px white, 1250px 20px white, 1180px 100px white,
    1500px 200px white, 250px 130px white, 250px 120px white, 200px 50px white,
    500px 50px white, 900px 50px white, 1400px 100px white, 320px 100px white;
}
@keyframes anim-stars {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-200px);
  }
}
.stars {
  animation: anim-stars 10s linear infinite;
}

.stars::after {
  content: " ";
  width: 3px;
  height: 3px;
  border-radius: 50%;
  box-shadow: 50px 30px white, 100px 80px white, 80px 120px white,
    300px 20px white, 250px 130px white, 200px 50px white, 150px 100px white,
    320px 100px white, 1320px 100px white;
}
.stars::after {
  content: " ";
  position: absolute;
  top: 150px;
  width: 3px;
}
@media only screen and (max-width: 700px) {
  .App {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
    height: 100vh;
    overflow-x: auto;
    background-image: url(./components/img/mobileAStro.jpeg);
  }
}

html,
body,
#root {
  min-height: 100vw;
  max-height: 100vw;
}
